import React from 'react';
import './App.css';

import {MuiThemeProvider} from '@material-ui/core/styles';
import theme from './theme.js';

import ControlPanel from './components/ControlPanel.js';
import FunctionPlot from './components/FunctionPlot.js';

import SidePanel from './components/SidePanel.js';
import SliderPanel from './components/SliderPanel/SliderPanel.js';
import OptionsPanel from './components/OptionsPanel.js';

import HelpText from './components/HelpText.js';


class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      expression: 'z',
      error: false,
      drawerOpen: false,
      helpOpen: false,
      variables: {
	log_scale: 5,
	center_x: 0,
	center_y: 0,
	t: 0.3,
	enable_checkerboard: 1
      }
    }
  }

  componentDidMount() {
    document.title = 'Complex Function Plotter';
  }

  // Toolbar buttons
  handleMenuButton() {
    this.setState({drawerOpen: !this.state.drawerOpen});
  }

  handleHelpButton() {
    this.setState({helpOpen: !this.state.helpOpen});
  }

  // Variable Sliders
  handleVariableUpdate(variables) {
    this.state.variables = variables;
    this.refs.plot.handleVariableUpdate(variables);
  }

  handleVariableAdd(name, value) {
    this.state.variables[name] = value;
    this.forceUpdate();
  }

  handleVariableRemove(name) {
    const variables = this.state.variables;
    delete variables[name];
    this.setState({variables: variables});
  }

  handleOptionToggle(name) {
    const variables = this.state.variables;
    variables[name] = (variables[name] < 0.5) ? 1 : 0;
    this.forceUpdate();
  }

  setErrorState(state) {
    if (state !== this.state.error) {
      this.setState({error: state});
    }
  }

  plotFunction(text) {
    this.setState({expression: text.toLowerCase()});
  }

  render() {
    return (
      <MuiThemeProvider theme={theme}>
	<div id='app'>
	  <ControlPanel
	    onMenuButton={() => this.handleMenuButton()}
	    onHelpButton={() => this.handleHelpButton()}
	    onChange={(text) => this.plotFunction(text)}
	    error={this.state.error}
	  />
	  <SidePanel
	    className='control-panel'
	    open={this.state.drawerOpen} 
	    onToggle={() => this.handleMenuButton()}
	    anchor='left'
	  >
	    <SliderPanel
	      variables={this.state.variables}
	      onUpdate={
		(variables) => this.handleVariableUpdate(variables)
	      }
	      onAdd={
		(name, value) => this.handleVariableAdd(name, value)
	      }
	      onRemove={
		(name) => this.handleVariableRemove(name)
	      }
	    />
	    <OptionsPanel
	      onToggle={(name) => this.handleOptionToggle(name)}
	      variables={this.state.variables}
	    />
	  </SidePanel>
	  <FunctionPlot
	    ref='plot'
	    expression={this.state.expression}
	    variables={this.state.variables}
	    onError={(state) => this.setErrorState(state)}
	  />
	  <SidePanel
	    open={this.state.helpOpen}
	    anchor='right'
	    onToggle={() => this.handleHelpButton()}
	  >
	    <HelpText/>
	  </SidePanel>
	</div>
      </MuiThemeProvider>
    );
  }
}

export default App;



