import React from 'react';

import {withStyles} from '@material-ui/core/styles';
import {Typography, Chip} from '@material-ui/core';
import MenuIcon from '@material-ui/icons/Menu';
import DeleteIcon from '@material-ui/icons/Delete';

import {functions, constants} from '../gl-code/parse-expression.js';

const styles = theme => ({
  heading: {
    marginTop: 25,
    marginBottom: 8
  },
  paragraph: {
    lineHeight: 2
  },
  buttonStyle: {
    ...theme.typography.button
  },
  chip: {
    margin: 3
  }
});

const supportedFunctions = ['+', '-', '*', '/', '^'];
supportedFunctions.push(...functions);
supportedFunctions.push(...constants);

var headingClass;

function Heading(props) {
  return (
    <Typography
      variant='title'
      className={headingClass}
    >{props.children}</Typography>
  );
}

class HelpText extends React.PureComponent {
  constructor(props) {
    super(props);
    this.classes = props.classes;
    headingClass = this.classes.heading;
  }
  
  renderSupportedFunctions() {
    return supportedFunctions.map(label =>
      <Chip 
	key={label}
	label={label}
	className={this.classes.chip}
      />
    );
  }

  render() {
    return (
      <div id='help-text'>

	<Typography variant='display1'>Complex Function Plotter</Typography>

	<Heading>Introduction</Heading>
	<p>Creates fast, interactive domain-colored plots of complex functions.</p>
	<p>Each point on the complex plane is colored according to the value of the function at that point.
	Hue and brightness are used to show the phase and magnitude of the function, respectively.</p>
	<p>To increase the displayable range of values, brightness cycles from dark to light repeatedly with magnitude, jumping at every power of two.
	Therefore, sudden transitions in brightness correspond to curves along which the function has constant magnitude.
	</p>
	<p>This method allows for quick visualization of the locations and orders of any poles and zeros, along with many other nice properties.
	For more information, see the <a href='https://en.wikipedia.org/wiki/Domain_coloring'>Wikipedia article on domain coloring.</a></p>

	<Heading>Controls</Heading>
	<p>To plot a complex function, enter its expression in the bar above. Parentheses (both curved and square), complex number literals, arbitrary variables (see below), and correct order of operations are supported. Input is case-insensitive.</p>
	<p>The entry field will show a red underline if there is an error parsing the expression. You may have made a syntax error or have forgotten to declare a variable.</p>
	<p>Drag the plot to pan, and use the scroll wheel to zoom in and out.</p>

	<Typography
	  variant='title'
	  className={this.classes.heading}
	>Supported Functions and Constants</Typography>
	{this.renderSupportedFunctions()}

	<Heading>Variables</Heading>

	<p>
	Click on the <MenuIcon style={{fontSize: 20}}/> icon in the upper-left corner to open the variables pane.
	</p>

	<p>
	Variables can be added with the <span className={this.classes.buttonStyle}>Add</span> button and removed with the <DeleteIcon style={{fontSize: 20}}/> button.
	Declared variables can be used in entered functions without restrictions.
	</p>

	<p>
	Values can be adjusted between 0 and 1 using the slider, allowing interactive visualization of a parameterized family of functions.
	</p>
	
	<p>Variables names can consist of any string of lowercase letters, except for reserved functions, the built-in variable z, and the constant i.</p>

	<Heading>Options</Heading>
	<p>Additional options can be found below the variables panel. Enabling the checkerboard will show a checkerboard overlay with eight squares per unit length.</p>

	<Heading>Acknowledgements</Heading>
	<p>Inspired by David Bau's <a href='http://davidbau.com/conformal'>Conformal Map Plotter</a>.</p>

	<Typography variant='caption'>Complex Functions Plotter — Made with love by Samuel J. Li</Typography>
      </div>
    );
  }
}

export default withStyles(styles)(HelpText);



