import React from 'react';

import {
  List, ListSubheader, ListItem, ListItemText,
  Checkbox
} from '@material-ui/core';


class OptionsPanel extends React.PureComponent {
  render() {
    return (
      <List id='options-panel'>
	<ListSubheader disableSticky>Options</ListSubheader>
	<ListItem
	  button
	  onClick={() => this.props.onToggle('enable_checkerboard')}
	>
	  <Checkbox
	    checked={this.props.variables.enable_checkerboard > 0.5}
	  />
	  <ListItemText>Enable Checkerboard</ListItemText>
	</ListItem>
      </List>
    );
  }
}

export default OptionsPanel;



